@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/HelveticaNeueCyr-Roman.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Roman.woff") format("woff"); }

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  src: local(""), url("../fonts/HelveticaNeueCyr-Bold.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 375px;
  margin: 0;
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  background-color: #343539;
  color: #ffffff;
  counter-reset: section-counter; }

.list-reset {
  padding: 0;
  margin: 0;
  list-style: none; }

.img {
  min-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  background-color: #ffffff;
  color: #343539;
  text-align: center;
  border: none;
  outline: none;
  display: block; }
  .button:hover, .button:focus {
    opacity: 0.3;
    cursor: pointer; }
  .button:active {
    opacity: 0.3; }

a {
  text-decoration: none;
  color: #ffffff; }

.title-small {
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  position: relative;
  left: 33px; }

.form__list {
  display: flex;
  width: 46%;
  flex-wrap: wrap; }

input {
  font-size: 15px;
  line-height: 18px;
  min-height: 46px; }

h2 {
  margin: 0;
  max-width: 200px; }

.form__item p {
  margin: 0;
  padding-bottom: 6px;
  font-size: 13px; }

@media (max-width: 1700px) {
  .title-small {
    font-size: 20px;
    line-height: 24px; }
  .form__list {
    width: 100%; }
  .form__item p {
    padding-bottom: 9px; }
  select {
    min-height: 45px; } }

.form {
  width: 100%;
  margin: 0 auto;
  padding-bottom: 129px; }

.form__container {
  margin: 0 auto;
  padding-top: 42px;
