.form__transport-dropdown {
  position: relative;
  display: inline-block;
  z-index: 4; }

.form__transport-content {
  display: none;
  position: absolute;
  background-color: #ffffff;
  width: 100%;
  z-index: 5; }

.content2 {
  right: 0; }

.form__transport-content a {
  color: #343539;
  padding: 12px 16px;
  text-decoration: none;
  display: block; }

.form__transport-content a:hover {
  background-color: #ef483f;
  color: #ffffff; }

.show {
  display: block; }

@media (max-width: 1700px) {
  .form__transport-list {
    width: 100%; } }

.form__cargo {
  padding-top: 42px; }

.form__cargo h2 {
  padding-bottom: 16px; }

.form__cargo-item:first-child {
  width: 100%; }

.form__cargo-list {
  width: 23%; }

.form__cargo-item {
  width: 50%;
  padding-right: 8px;
  padding-bottom: 15px; }

.form__cargo-item:first-child,
.form__cargo-item:nth-child(4),
.form__cargo-item:nth-child(5) {
  width: 100%; }

.form__cargo-item:nth-child(1),
.form__cargo-item:nth-child(3) {
  padding-right: 0; }

.form__cargo-dropbtn {
  background-color: #ffffff;
  color: #343539;
  font-size: 15px;
  border: none;
  cursor: pointer;
  display: block;
  width: 100%;
  padding: 13px;
  justify-content: space-between;
  display: flex;
  align-items: center; }

.form__cargo-dropbtn:hover,
.dropbtn:focus {
  position: relative;
  color: #cfcfcf;
  z-index: 3;
  border: none; }

.form__cargo-dropbtn .form__cargo-arrow,
.form__cargo-dropbtn .form__cargo-arrow {
  display: block; }

.form__cargo-dropbtn .form__cargo-arrow--active:hover,
.form__cargo-dropbtn .form__cargo-arrow--active:focus {
  display: block; }

.form__cargo-dropbtn .form__cargo-arrow,
.form__cargo-dropbtn .form__cargo-arrow {
  display: none; }

.form__cargo-dropdown {
  position: relative;
  display: inline-block;
  z-index: 3; }

.form__cargo-content {
  display: none;
  position: absolute;
  background-color: #ffffff;
  width: 100%;
  z-index: 5; }

.form__cargo-content a {
  color: #343539;
  padding: 12px 16px;
  text-decoration: none;
  display: block; }

.form__cargo-content a:hover {
  background-color: #ef483f;
  color: #ffffff; }

.show .form__cargo-arrow {
  display: none; }

.checkbox__arrow {
  display: flex;
  position: relative;
  font-size: 13px;
  line-height: 15px;
  border: none;
  background-color: inherit;
  color: #ffffff;
  padding-bottom: 4px;
  align-items: center; }

.form__cargo-item {
  padding-bottom: 11px; }

.checkbox__arrow::before {
  background-color: #343539;
  border: 1px solid #fff;
  margin-right: 11px; }

.checkbox input:checked + label::after {
  content: "";
  width: 6px;
  height: 10px;
  transform: rotate(40deg);
  background-color: #ef483f;
  border-right: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  margin: 0;
  background-position: center;
  display: inline-block;
  vertical-align: top;
  z-index: 1;
  position: absolute;
  left: 11px;
  top: 5px; }

.checkbox input:checked + .checkbox__arrow::before {
  background-color: #ef483f;
  border: none; }

.form__temperature-list {
  padding-top: 17px;
  display: flex;
  width: 100%; }

.form__temperature-item {
  padding-right: 8px;
  width: 50%; }

.form__temperature-content {
  display: none; }

.form__temperature-item:last-child {
  padding-right: 0; }

.form__temperature-item label {
  font-size: 13px;
  line-height: 15px;
  padding-bottom: 5px; }

@media (max-width: 1700px) {
  .form__cargo-list {
    width: 100%; }
  .form__cargo-item:first-child,
  .form__cargo-item:nth-child(5) {
    width: 50%; }
  .form__cargo-item:nth-child(1) {
    padding-right: 8px; }
  .form__cargo-item:nth-child(2) {
    display: none; }
  .form__cargo-item:nth-child(3) {
    padding-right: 0; }
  .form__cargo-item:nth-child(4) {
    width: 100%;
    padding-right: 0; }
  .checkbox__arrow::before {
    content: "";
    width: 25px;
    height: 25px; }
  .form__temperature-list {
    display: flex; }
  .form__temperature-item {
    width: 50%; }
  .form__temperature-item {
    padding-right: 8px; }
  .form__temperature-item:last-child {
    padding-right: 0; } }

.form__time {
  padding-top: 19px; }

.form__time-box {
  width: 46%; }

.form__time-wrap {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  line-height: 15px; }

.form__time-wrap p {
