  margin-top: 9px;
  margin-right: 8px; }

.form__time-uninsert {
  position: relative;
  font-size: 15px;
  line-height: 19px;
  padding-top: 13px;
  left: 37px; }

.form__time-insert {
  font-size: 13px;
  line-height: 16px; }

.disable .form__time-insert {
  opacity: 0.3; }

.form__time-uninsert::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: #343539;
  border-radius: 50%;
  border: 4px solid #ef483f;
  left: -22px; }

.disable .form__time-uninsert::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: #343539;
  border-radius: 50%;
  border: 4px solid #ffffff;
  left: -22px; }

.form__item-letter {
  padding: 12px 0 8px 0; }

.form__time-text {
  font-size: 15px;
  line-height: 18px;
  padding: 14px 0 0 14px;
  color: #cfcfcf;
  font-family: "Helvetica", "Arial", sans-serif;
  min-height: 80px;
  resize: vertical; }

.form__time-list {
  width: 100%; }

@media (max-width: 1700px) {
  .form__time {
    padding-top: 22px; }
  .form__time-box {
    width: 100%; }
  .form__time-text {
    padding: 14px 10px 0 14px; } }

.dropbtn {
  width: 103px;
  cursor: pointer;
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  color: #ffffff;
  background-color: #343539;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.dropbtn:hover,
.dropbtn:focus {
  opacity: 0.3; }

.dropdown {
  position: relative;
  display: inline-block; }

.dropdown-content {
  display: none;
  overflow: auto; }

.show {
  display: block; }

@media (max-width: 1700px) {
  .dropbtn {
    font-size: 20px;
    line-height: 24px; } }

.section {
  position: relative;
  counter-increment: section-counter; }

.section::before {
  content: counter(section-counter);
  position: absolute;
  width: 24px;
  height: 24px;
  background-color: #ef483f;
  border-radius: 50%;
  color: #ffffff;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  vertical-align: middle;
  left: 0; }

.form__info {
  max-width: 356px;
  margin: 0 auto;
  background-color: #4b4b4f;
  font-size: 15px;
  line-height: 18px;
  position: absolute;
  top: 126px;
  left: 50%;
  transform: translateX(-46px); }

.form__info p {
  padding: 12px 30px 7px 25px; }

@media (max-width: 1700px) {
